import { uuid } from '../utils/uuid'

const app = getApp({ allowDefault: true })

export const rsVrWebviewPropsWrapper = (object: Record<any, ((...args: any[]) => any) | any>) => {
  if (!app.__RsVrWebviewPropsMap__) {
    app.__RsVrWebviewPropsMap__ = new Map()
  }
  const key = uuid()
  app.__RsVrWebviewPropsMap__.set(key, object)

  return key
}

export const getRsVrWebviewProps = (keys: string | string[]) => {
  const _keys = Array.isArray(keys) ? keys : [keys]
  const props: Record<string, any> = {}
  _keys.forEach((key) => {
    props[key] = app.__RsVrWebviewPropsMap__.get(key)
  })
  if (!Array.isArray(keys)) {
    return props[keys]
  }
  return props
}

export const removeRsVrWebviewProps = (keys: string | string[]) => {
  const _keys = Array.isArray(keys) ? keys : [keys]
  _keys.forEach((key) => {
    app.__RsVrWebviewPropsMap__.delete(key)
  })
}
